.leftcont{
    h1{
    font-size: 25px;
    line-height: 42px;
    font-weight: 700;
    }
    h3{
      font-size: 24px;
      line-height: 30px;
    }
}

.chat{
    margin-top: auto;
    margin-bottom: auto;
}
.card.cardchat {
    height: auto;
    border-radius: 15px !important;
    // background-color: rgba(0,0,0,0.4) !important;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border: none;
}
.contacts_body{
    padding:  0.75rem 0 !important;
    // overflow-y: auto;
    // white-space: nowrap;
}
.msg_card_body{
    overflow-y: auto;
}
.card-header{
    border-radius: 15px 15px 0 0 !important;
    border-bottom: 0 !important;
}
.card-footer{
border-radius: 0 0 15px 15px !important;
    border-top: 0 !important;
}
.container{
    align-content: center;
}
.search{
    border-radius: 15px 0 0 15px !important;
    background-color: rgba(0,0,0,0.3) !important;
    border:0 !important;
    color:white !important;
}
.search:focus{
     box-shadow:none !important;
   outline:0px !important;
}
.type_msg{
    background-color: #fff !important;
    border:0 !important;
    color:#000 !important;
    height: 60px !important;
    overflow-y: auto;
}
    .type_msg:focus{
     box-shadow:none !important;
   outline:0px !important;
}
.attach_btn{
border-radius: 15px 0 0 15px !important;
background-color: #fff !important;
    border:0 !important;
    color: #000 !important;
    cursor: pointer;
    padding: 22px;
}
.send_btn{
border-radius: 0 15px 15px 0 !important;
background-color: #4caf50!important;
    border:0 !important;
    color: #fff  !important;
    cursor: pointer;
    padding: 22px;
}
.search_btn{
    border-radius: 0 15px 15px 0 !important;
    background-color: rgba(0,0,0,0.3) !important;
    border:0 !important;
    color: white !important;
    cursor: pointer;
    padding: 14px;
}
.contacts{
    list-style: none;
    padding: 0;
}
.contacts li{
    width: 100% !important;
    padding: 5px 10px;
    margin-bottom: 15px !important;
}
.active{
    background-color: rgba(0,0,0,0.3);
}
